/* Navigation.css */

nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: black;
}

ul {
    list-style: none;  /* Remove default bullet points */
    margin: 0;
    padding: 0;
    display: flex;
    gap: 15px;  /* Space between each navigation item */
}

li {
    position: relative;
}

li a {
    display: inline-block;
    padding: 10px 20px;
    color: white; /* Text color */
    text-decoration: none; /* Remove underline from links */
    border-radius: 20px; /* Rounded corners */
    background: radial-gradient(circle, #8e44ad, rgba(142, 68, 173, 0)); /* Radial gradient from purple to transparent */
    transition: background 0.3s ease, transform 0.2s ease;
}

li a:hover {
    background: radial-gradient(circle, #9b59b6, rgba(155, 89, 182, 0)); /* Slightly lighter purple on hover */
    transform: scale(1.05); /* Slightly enlarge the button on hover */
}

li a:active {
    transform: scale(0.95); /* Slightly shrink the button on click */
}
